import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewDefect from '@/components/NewDefect'
import ViewDefect from '@/components/ViewDefect'
import EditDefect from '@/components/EditDefect'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-defect',
      component: NewDefect
    },
    {
      path: '/edit/:reg',
      name: 'edit-defect',
      component: EditDefect
    },
    {
      path: '/:reg',
      name: 'view-defect',
      component: ViewDefect
    }
  ]
})



