<template>
  <div id="new-defect">
      <h3>New Defect Record</h3>
      <div class="row">
          <form @submit.prevent="saveDefect" class="col s12">
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="reg" required>
                      <label for="reg">Registration</label>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="description" required>
                      <label for="description">Description</label>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="date" v-model="date_recieved" required>
                      <label for="date_recieved">Date Recieved</label>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="comments" >
                      <label for="comments">Comments</label>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="belongs_to" required>
                      <label for="belongs_to">Belongs to</label>
                  </div>
              </div>
              <button type="submit" class="btn">Submit</button>
              <router-link to="/" class="btn grey">Back</router-link>
          </form>
      </div>
  </div>
    
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-defect',
  data () {
      return {
        reg: null,
        description: null,
        date_recieved: null,
        date_rectified: null,
        comments: null, 
        belongs_to: null
      }
  },
  methods: {
     saveDefect ()  {
         db.collection('defects').add({
             reg: this.reg,
             description: this.description,
             date_recieved: this.date_recieved,
             comments: this.comments,
             belongs_to: this.belongs_to
         })
         .then(docRef => {this.$router.push('/')})
         .catch(error => confirm.log(error))
     }
  }
}
</script>




