<template>
  <div id="dashboard">
      <table>
          <thead>
              <tr>
                  <th>Reg</th>
                  <th>Description</th>
                  <th>Date Recieved</th>
                  <th>Date Rectified</th>
                  <th>Comments</th>
                  <th>Beleongs to</th>
                  <th></th>
              </tr>
          </thead>
          <tbody>
              <tr v-for="defect in defects" v-bind:key="defect.reg">
                  <td>{{defect.reg}}</td>
                  <td>{{defect.description}}</td>
                  <td>{{defect.date_recieved}}</td>
                  <td>{{defect.date_rectified}}</td>
                  <td>{{defect.comments}}</td>
                  <td>{{defect.belongs_to}}</td>
                  <td>
                        <router-link v-bind:to="{name: 'view-defect', params: {reg: defect.reg}}">
                        <i class="fa fa-eye"></i>
                        </router-link>
                  </td>
              </tr>
          </tbody>
      </table>
      <div class="fixed-action-btn">
          <router-link to="/new"
          class="btn-floating pulse btn-large red">
          <i class="material-icons"><i class="fa fa-plus"></i></i>
          </router-link>
      </div>
  </div>

</template>

<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data () {
      return {
          defects: []
          
      }
  },
  created () {
      db.collection('defects').get().then
        (querrySnapshot => {
            querrySnapshot.forEach(doc => {
                const data = {
                    'id': doc.data().id,
                    'reg': doc.data().reg,
                    'description': doc.data().description,
                    'date_recieved': doc.data().date_recieved,
                    'date_rectified': doc.data().date_rectified,
                    'comments': doc.data().comments,
                    'belongs_to': doc.data().belongs_to
                }
                this.defects.push(data)
            })
        })
  }
}
</script>




