<template>
  <div id="view-defect">
      <ul class="collection with-header">
          <li class="collection-header">
              <h4>{{reg}}</h4>
          </li>
          <li class="collection-item"><b>Defect Description:</b> {{description}}</li>
          <li class="collection-item"><b>Defect Date:</b> {{date_recieved}}</li>
          <li class="collection-item"><b>Defect Rectified Date:</b> {{date_rectified}}</li>
          <li class="collection-item"><b>Comments:</b> {{comments}}</li>
          <li class="collection-item"><b>Belongs To:</b> {{belongs_to}}</li>
      </ul>
      
        <div class="row">
            <router-link to="/" class="btn grey">Back</router-link>
        </div>
      <div class="fixed-action-btn">
          <router-link :to="{name: 'edit-defect', params: {reg: reg}}"
          class="btn-floating pulse btn-large green">
          <i class="material-icons"><i class="fa fa-pencil"></i></i>
          </router-link>
      </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-defect',
  data () {
      return {
        reg: null,
        description: null,
        date_recieved: null,
        date_rectified: null,
        comments: null, 
        belongs_to: null
      }
  },
  beforeRouteEnter (to, from, next) {
      db.collection('defects').where('reg', '==', to.params.reg).get()
      .then(querrySnapShot => {
          querrySnapShot.forEach(doc => {
              next(vm => {
                  vm.reg = doc.data().reg
                  vm.description = doc.data().description
                  vm.date_recieved = doc.data().date_recieved
                  vm.date_rectified = doc.data().date_rectified
                  vm.comments = doc.data().comments
                  vm.belongs_to = doc.data().belongs_to
              })
          })
      })
  },
  watch: {
      '$route': 'fetchData'
  },
  methods: {
      fetchData () {
          db.collection('defects').where('reg', '==', this.$route.params.reg).get()
          .then(querrySnapShot.forEach(doc => {
              this.reg = doc.data().reg
              this.description = doc.data().description
              this.date_recieved = doc.data().date_recieved
              this.date_rectified = doc.data().date_rectified
              this.comments = doc.data().comments
              this.belongs_to = doc.data().belongs_to
          }))
      }
  }
}
</script>




