<template>
  <div id="edit-defect">
      <h3>Update Defect {{reg}}</h3>
      <div class="row">
          <form @submit.prevent="updateDefect" class="col s12">
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="description" required>
                  </div>
              </div>
              <div class="row">
                  <div class="col s6">
                      Date Recieved: <input type="date" v-model="date_recieved" required>
                    </div>
                 <div class="col s6">
                     Date Rectified: <input type="date" v-model="date_recieved" required>
                 </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="comments" >
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="belongs_to" required>
                  </div>
              </div>
              <button type="submit" class="btn">Update</button>
              <router-link to="/" class="btn grey">Back</router-link>
          </form>
      </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-defect',
  data () {
      return {
        reg: null,
        description: null,
        date_recieved: null,
        date_rectified: null,
        comments: null, 
        belongs_to: null 
      }
  },
  beforeRouteEnter (to, from, next) {
      db.collection('defects').where('reg', '==', to.params.reg).get()
      .then(querrySnapShot => {
          querrySnapShot.forEach(doc => {
              next(vm => {
                  vm.reg = doc.data().reg
                  vm.description = doc.data().description
                  vm.date_recieved = doc.data().date_recieved
                  vm.date_rectified = doc.data().date_rectified
                  vm.comments = doc.data().comments
                  vm.belongs_to = doc.data().belongs_to
              })
          })
      })
  },
  watch: {
      '$route': 'fetchData'
  },
  methods: {
      fetchData () {
          db.collection('defects').where('reg', '==', this.$route.params.reg).get()
          .then(querrySnapShot => {
              querrySnapShot.forEach(doc => { 
              this.reg = doc.data().reg
              this.description = doc.data().description
              this.date_recieved = doc.data().date_recieved
              this.date_rectified = doc.data().date_rectified
              this.comments = doc.data().comments
              this.belongs_to = doc.data().belongs_to
              })
          })  
      },
      updateDefect () {
          db.collection('defects').where('reg', '==', this.$route.params.reg).get()
          .then(querrySnapShot => {
              querrySnapShot.forEach(doc => { 
              doc.ref.update({
                reg: this.reg,
                description: this.description,
                date_recieved: this.date_recieved,
                date_rectified: this.date_rectified,
                comments: this.comments,
                belongs_to: this.belongs_to
              })
              .then(() => {
                  this.$router.push({name: 'view-defect', 
                  params: {reg: this.reg}})
              })
              })
          })  
      }
  }
}
</script>

                




